.section-card {
  margin-bottom: 20px;
  padding: 15px;
  border-radius: 8px;
}

.treatments-card { background-color: #e6f3ff; }
.add-ons-card { background-color: #f3e6ff; }
.bonuses-card { background-color: #fff3e6; }

.row-headers, .row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.row-headers > div,
.row > * {
  padding: 0 5px;
}

.header-treatment-name,
.treatment-name { flex: 3; }

.header-price,
.price,
.header-count,
.count,
.header-total,
.total { 
  flex: 1; 
  max-width: 80px;
  text-align: right;
}

select, input[type="number"] {
  width: 100%;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.summary {
  background-color: #f8f9fa;
  border-radius: 8px;
  padding: 15px;
  margin-top: 20px;
}

.summary h2 {
  margin-top: 0;
  margin-bottom: 15px;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.summary-row .label {
  flex: 1;
  font-weight: bold;
}

.summary-row .value,
.summary-row input {
  flex: 0 0 100px;
  text-align: right;
}

.summary-row input {
  width: 100px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.total-with-bonuses {
  font-weight: bold;
  color: #007bff;
  border-top: 2px solid #007bff;
  padding-top: 10px;
  margin-top: 10px;
}














